import React from 'react';
import './Saved.css';

const Saved = ({ likedCards }) => {
  return (
    <div className="saved">
      <h1>Saved Cards</h1>
      <div className="saved--cards">
        {/* Ensure that likedCards is defined before attempting to map over it */}
        {likedCards && likedCards.length > 0 ? (
          likedCards.map((card) => (
            <div key={card.id} className="saved--card">
              <img src={card.image} alt={card.name} />
              <h3>{card.name}</h3>
              <p>{card.description}</p>
            </div>
          ))
        ) : (
          <p>No saved cards yet.</p>
        )}
      </div>
    </div>
  );
};

export default Saved;